(function($){

  $(window).on('load', function() {
    $('.loader').hide();
    $('body').addClass('loaded');
  });

  /* Sticky Header */
  $(function() {
    let header = $("#header");
    let yOffset = 0;
    let triggerPoint = 80;

    $(window).on( 'scroll', function() {
      yOffset = $(window).scrollTop();

      if (yOffset >= triggerPoint) {
        header.addClass("navbar-fixed-top");
      } else {
        header.removeClass("navbar-fixed-top");
      }
    });
  });

  /* Mobile Menu */
  $('.menu-wrap ul.nav').slicknav({
    prependTo: '.header_section .navbar',
    label: '',
    allowParentLinks: true,
    closeOnClick:true,
  });

  /* ScreenShot Carousel */
  function getSlide() {
    let wW = $(window).width();
    if (wW < 1080) {
      return 1;
    }
    return 3;
  }

  let mySwiper = new Swiper('.screen_carousel', {
    direction:'horizontal',
    loop: true,
    zoom: true,
    autoplay: {
      delay: 5000,
      disableOnInteraction: false,
    },
    effect: 'coverflow',
    centeredSlides: true,
    slidesPerView: 2, // getSlide(),
    spaceBetween: 10,
    breakpoints: {
      // when window width is >= 320px
      480: {
        slidesPerView: 2,
        // spaceBetween: 20
      },
      // when window width is >= 480px
      640: {
        slidesPerView: 3,
        // spaceBetween: 30
      },
    },
    grabCursor: true,
    coverflowEffect: {
      rotate: 30,
      stretch: -30,
      depth: 80,
      modifier: 1,
      slideShadows: false
    },
    pagination: {
      el: '.screen-pagination',
      clickable: true,
    },
  });

  setTimeout(() => {
    mySwiper.update();
  }, 50)

  // window.onresize = function(event) {
  //
  //   mySwiper.
  //   mySwiper.update();
  // };

  let scroll = new SmoothScroll('a[href*="#"]', {
    speed: 500,
    offset: 60,
    speedAsDuration: true
  });


  /* Scroll To Top */
  $(window).on( 'scroll', function () {
    if ($(this).scrollTop() > 100) {
      $('#scroll-to-top').fadeIn();
    } else {
      $('#scroll-to-top').fadeOut();
    }
  });

})($);
